import { makeStyles, Typography } from '@material-ui/core';
import React from 'react';

interface BonFireGroupsProps {
    header: string;
    src: string;
}

const useStyles = makeStyles((theme) => ({
    container: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        padding: `${theme.spacing(2)}px ${theme.spacing(5)}px`,
        '&:hover': {
            cursor: 'pointer',
        },
        '&:hover > img': {
            transform: `scale(1.1)`,
            transition: `200ms ease-in-out,200ms ease-in-out`,
        },
    },
    img: {},
    subContent: {
        fontFamily: 'roboto, sans-serif',
        fontSize: '20px',
        marginTop: '10px',
    },
}));

const BonFireGroups: React.FC<BonFireGroupsProps> = ({ header, src }) => {
    const classes = useStyles();
    return (
        <div className={classes.container}>
            <img src={src} className={classes.img} />
            <Typography className={classes.subContent} style={{ textAlign: 'center', fontSize: '20px' }}>
                {header}
            </Typography>
        </div>
    );
};

export default BonFireGroups;
