import { makeStyles } from '@material-ui/core';

export const useStyles = makeStyles((theme) => ({
    pageContainer: {
    },
    padContainer: {
        padding: theme.spacing(5),
    },
    header: {
        padding: `0px ${theme.spacing(10)}px`,
        paddingTop: '280px',
        backgroundColor: 'rgb(247, 246, 244)',
    },
    headerLeft: {
        display: 'flex',
    },
    supplyContainerContainer: {
        minWidth: '600px',
    },
    headerRightImg: {
        width: '100%',
        minWidth: '700px',
        height: '100%',
    },
    iconTxtContainer: {
        display: 'flex',
        alignItems: 'center',
        margin: `0px ${theme.spacing(3)}px`,
    },
    subheader: {
        fontSize: '35px',
        fontWeight: 600,
        fontFamily: 'Soleil, Helvetica, Arial, sans-serif',
    },
    designContainer: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-start',
        paddingLeft: theme.spacing(6),
        paddingTop: '20px',
    },
    supply: {
        borderRight: '2px solid rgba(127,127,127,0.3)',
        paddingRight: '20px',
        width: '116px',
        height: '40px',
        transform: `scale(1.3)`,
        minWidth: '100px',
    },
    supplyContainer: {
        display: 'flex',
        alignItems: 'center',
        paddingTop: '20px',
    },
    ordinaryText: {
        marginLeft: '10px',
        fontSize: '17px',
    },
    iconTxtContainerContainer: {
        display: 'flex',
        justifyContent: 'center',
        marginTop: '80px',
        marginBottom: '80px',
    },
    mainTxt: {
        fontSize: '30px',
        fontWeight: 600,
    },
    seperator: {
        position: 'relative',
    },
    dot: {
        width: '15px',
        height: '15px',
        backgroundColor: '#bdcad8',
        borderRadius: '50%',
    },
    dotAbsolute: {
        position: 'absolute',
        top: '50%',
        transform: 'translateY(-50%)',
    },
    lastConnector: {
        marginBottom: '115px',
        [theme.breakpoints.between(1046, 1280)]: {
            marginBottom: '175px',
        },
        [theme.breakpoints.between(992, 1046)]: {
            marginBottom: '175px',
        },
    },
    centered: {
        display: 'flex',
        justifyContent: 'center',
        marginTop: '40px',
    },
    subContent: {
        fontFamily: 'roboto, sans-serif',
        lineHeight: '1.5',
        fontSize: '20px',
        marginTop: '40px',
    },
    soleil: {
        fontFamily: 'Soleil, Helvetica, Arial, sans-serif',
    },
    rightPart: {
        padding: theme.spacing(10),
        textAlign: 'left',
    },
    leftImg: {
        transform: 'scale(1.01)',
        minHeight: '100%',
    },
    blackCont: {
        backgroundColor: '#3d4853',
    },
    talkToHuman: {
        alignSelf: 'flex-start',
        marginLeft: theme.spacing(15),
        marginTop: '20px',
        [theme.breakpoints.down('md')]: {
            alignSelf: 'center',
            marginBottom: '50px'
        }
    },
    leftRightImgCont: {
        border: `2px solid rgba(127,127,127,.1)`,
        padding: 0,
        marginTop: '80px',
        marginBottom: '80px',
    },
    grayBg: {
        backgroundColor: '#f1f4f8',
        minWidth: '100% !important',
        margin: 0,
        paddingBottom: '80px',
    },
    whiteBg: {
        backgroundColor: 'white',
    },
    marginleft: {
        paddingLeft: theme.spacing(7),
        [theme.breakpoints.only('md')]: {
            paddingLeft: theme.spacing(1),
        },
        [theme.breakpoints.up('lg')]: {
            paddingLeft: theme.spacing(16),
        },
        [theme.breakpoints.between(1280, 1875)]: {
            paddingLeft: theme.spacing(7),
        },
        [theme.breakpoints.down(960)]: {
            paddingLeft: 0,
            width: '100%',
        },
    },
    avatar: {
        width: '80px',
        height: '80px',
        borderRadius: '50%',
        marginTop: '45px',
    },
    avatarCont: {
        padding: `${theme.spacing(5)}px ${theme.spacing(10)}px`,
    },
    bgTextureCont: {
        display: 'flex',
        justifyContent: 'center',
        backgroundImage: `url(${require('./svg/texture.png')})`,
    },
    author: {
        fontFamily: 'Soleil, Helvetica, Arial, sans-serif',
        marginLeft: theme.spacing(4),
        marginTop: '50px',
        fontSize: '20px',
    },
    address: {
        textAlign: 'left',
        maxWidth: '550px',
        marginLeft: theme.spacing(4),
    },
    headerml: {
        marginTop: '100px',
        maxWidth: '550px',
        marginLeft: theme.spacing(5),
        [theme.breakpoints.down('md')]: {
            textAlign: 'center'
        }
    },
    mobCenter: {
        [theme.breakpoints.down('md')]: {
            display: 'flex',
            justifyContent: 'center'
        }
    },
    ml: {
        marginLeft: theme.spacing(5),
        [theme.breakpoints.down('md')]: {
            display: 'flex',
            justifyContent: 'center',
            textAlign: 'center'
        }
    },
    mobThree: {
        marginTop: '100px',
        [theme.breakpoints.down('md')]: {
            marginTop: '0px',
        }
    },
    bonfireCont: {
        display: 'flex',
        justifyContent: 'center',
        padding: `${theme.spacing(2)}px ${theme.spacing(9)}px`,
        marginTop: '40px',
        marginBottom: '60px',
    },
    quote: {
        fontWeight: 400,
        position: 'relative',
    },
    smallQuote: {
        position: 'absolute',
        color: '#5e82a6',
        fontSize: '4.8rem',
        fontStyle: ' italic',
        width: '100px',
        fontWeight: 600,
        left: '-30px',
        top: '-10px',
    },
    hover: {
        position: 'relative',
        cursor: 'pointer',
        '& .btn-hover': {
            position: 'absolute',
            top: '50%',
            left: '50%',
            padding: '1.7rem',
            transform: 'translate(-50%, -50%)',
            opacity: 0,
            transition: '.5s',
            marginTop: 100,
        },
        '&:hover': {
            '& .btn-hover': {
                opacity: 1,
                marginTop: 0,
            },
        },
    },
    leftImgMob: {
        [theme.breakpoints.down('sm')]: {
            display: 'flex',
            justifyContent: 'center'
        }
    }
}));
