import { Grid, Typography } from '@material-ui/core';
import { Timeline, TimelineConnector, TimelineContent, TimelineItem, TimelineSeparator } from '@material-ui/lab';
import clsx from 'clsx';
import Feedback from 'pages/HomePage/Feedback';
import React from 'react';
import fightForFuture from '../../img/fightForFuture.jpg';
import orderShirt from '../../img/order_shirt_background.png';
import support2x from '../../img/support@2x.jpg';
import '../../sass/css/style.scss';
import Footer from '../Footer';
import Navbar from '../Header';
import { TimeLineBox } from './components';
import BonFireGroups from './components/BonFireGroups';
import { useStyles } from './custom.style';
import { ReactComponent as Discount } from './svg/discount.svg';
